.footer__content{
    width: 100%;
    height: 70px;
    border-top:1px solid #E6E6E6;
    border-bottom:1px solid #E6E6E6;
    margin:50px auto 0px auto;
  }
  .footer__content__container{
      width:80%;
      max-width: 100%;
      margin:0 auto;
      height: 70px;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      align-items: center;
  }
  .footer__content__container_left{
    color: #cccccc;
    font-family: $MonsterapReg;
    font-size: 13px;
    font-weight: 400;
    line-height: 24px;
  }
  @include IpadeLow{
      .footer__content__container_right{
          display: none;
      }
  }
  @include IpadReverse{
      .footer__content__container_left{
        margin:0 auto;
      }
      .footer__content__container_center{
          margin:0 auto;
      }
  }